import React, { useEffect, useState, useRef } from "react";
import { BASE_URL, API, API_USER, API_PASSWORD } from "../config";
import L from "leaflet";
import "leaflet/dist/leaflet.css";

const CHXDGMap = () => {
  const mapRef = useRef(null);
  const groupRef = useRef(null);
  const [coords, setCoords] = useState([]);
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState("");

  //  Gi API SAP
  const fetchCHXDList = async () => {
    try {
      setLoading(true);
      const token = btoa(`${API_USER}:${API_PASSWORD}`);
      const res = await fetch(`${BASE_URL}${API}`, {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
          Authorization: `Basic ${token}`,
        },
        body: JSON.stringify({ FUNC: "ZFM_CHXD_GMAP" }),
      });

      if (!res.ok) throw new Error(`HTTP error! status: ${res.status}`);
      const data = await res.json();

      if (Array.isArray(data?.RESPONSE?.T_DATA) && data.RESPONSE.T_DATA.length > 0) {
        const list = data.RESPONSE.T_DATA
          .map((i) => ({
            id: i.CHXD_ID,
            title: i.CHXD_TXT || "Ca hng khng tn",
            lat: parseFloat(i.ZLAT),
            lng: parseFloat(i.ZLONG),
            address: i.ADDRESS || "",
            phone: i.PHONE || "",
            logo: i.LOGO || "PLX",
          }))
          .filter((x) => !isNaN(x.lat) && !isNaN(x.lng));
        setCoords(list);
      } else {
        // D liu mu nu API rng
        setCoords([
          {
            title: "CHXD Petrolimex H Ni",
            lat: 21.0285,
            lng: 105.8542,
            address: "Hon Kim, H Ni",
            phone: "024-1234567",
          },
          {
            title: "CHXD Petrolimex  Nng",
            lat: 16.0678,
            lng: 108.2208,
            address: "Hi Chu,  Nng",
            phone: "0236-7654321",
          },
          {
            title: "CHXD Petrolimex TP.HCM",
            lat: 10.7769,
            lng: 106.7009,
            address: "Qun 1, TP.HCM",
            phone: "028-9876543",
          },
        ]);
      }
    } catch (err) {
      console.error(" Li fetchCHXDList:", err);
      setError("Khng th kt ni ti API hoc d liu li.");
    } finally {
      setLoading(false);
    }
  };

  //  Load d liu khi mount
  useEffect(() => {
    fetchCHXDList();
  }, []);

  //  To bn 
  useEffect(() => {
    if (!mapRef.current) {
      mapRef.current = L.map("map", {
        center: [15.5, 107],
        zoom: 6,
      });

      // openstreetmap, opentopomap
      L.tileLayer("https://server.arcgisonline.com/ArcGIS/rest/services/World_Imagery/MapServer/tile/{z}/{y}/{x}", {
      // L.tileLayer("https://{s}.tile.opentopomap.org/{z}/{x}/{y}.png", {
        maxZoom: 19,
        attribution: '&copy; OpenStreetMap contributors',
      }).addTo(mapRef.current);
    }
  }, []);

  //  Render marker
  useEffect(() => {
    if (!mapRef.current || coords.length === 0) return;
    const map = mapRef.current;

    if (groupRef.current) map.removeLayer(groupRef.current);
    const group = L.featureGroup();    

    coords.forEach((c) => {      
      //  Rt gn tn (nu > 16 k t)
      const shortTitle = c.title.length > 16 ? c.title.slice(0, 16) + "" : c.title;

      let iconUrl;
      switch (c.logo) {
        case "PLX":
          iconUrl = process.env.PUBLIC_URL + "/logo_plx.png";
          break;
        case "PVOIL":
          iconUrl = process.env.PUBLIC_URL + "/logo_pvoil.png";
          break;
        default:
          iconUrl = process.env.PUBLIC_URL + "/logo_default.png";
          break;
      }

      const markerIcon = L.icon({
        iconUrl,
        iconSize: [28, 28],
        iconAnchor: [14, 28],
        popupAnchor: [0, -25],
      });

      const marker = L.marker([c.lat, c.lng], { icon: markerIcon })
        .bindPopup(`
          <div style="font-size:14px; line-height:1.4;">
            <b>${c.title}</b><br/>
             <i>${c.address}</i><br/>
             ${c.phone || "N/A"}<br/>
             (${c.lat.toFixed(4)}, ${c.lng.toFixed(4)})
          </div>
        `);

      marker.on("mouseover", () => marker.openPopup());
      marker.on("mouseout", () => marker.closePopup());
      
      //  Nhn hin th y  tn trm
      const label = L.divIcon({
        className: "plx-label",
        html: `<div style="
          background: rgba(255,255,255,0.95);
          border: 1px solid #2a5599;
          border-radius: 6px;
          padding: 2px 5px;
          font-size: 11px;
          color: #2a5599;
          display: inline-block;
          white-space: nowrap;
          box-shadow: 0 1px 2px rgba(0,0,0,0.2);
          margin-left: 6px;
        ">${c.title}</div>`,
        iconSize: null,
        iconAnchor: [-5, 15], //  t cnh phi marker
      });

      const textMarker = L.marker([c.lat, c.lng], { icon: label, interactive: false });

      group.addLayer(marker);
      group.addLayer(textMarker);
    });

    group.addTo(map);
    groupRef.current = group;
    map.fitBounds(group.getBounds(), { padding: [40, 40] });
  }, [coords]);

  return (
    <div style={{ height: "100vh", position: "relative" }}>
      {loading && (
        <div
          style={{
            position: "absolute",
            top: "50%",
            left: "50%",
            transform: "translate(-50%, -50%)",
            fontSize: 18,
            color: "#2a5599",
          }}
        >
           ang ti d liu trm xng...
        </div>
      )}
      {error && (
        <div
          style={{
            position: "absolute",
            top: "50%",
            left: "50%",
            transform: "translate(-50%, -50%)",
            color: "red",
          }}
        >
           {error}
        </div>
      )}
      <div id="map" style={{ height: "100vh", width: "100%" }} />
    </div>
  );
};

export default CHXDGMap;
