import React, { useEffect, useState } from "react";
import { useSearchParams } from "react-router-dom";
import QRCodeImg from "./QRCodeImg";
import BarcodeImg from "./BarcodeImg";
import logoPetro from "../assets/logo_petro.png";
import { BASE_URL, API, API_USER, API_PASSWORD } from "../config";

const YCTN = ({ width = 450 }) => {
  const [searchParams] = useSearchParams();
  const maMau = searchParams.get("maMau") || "0123456789";

  const [info, setInfo] = useState(null);

  useEffect(() => {
    const fetchInfo = async () => {
      try {
        const token = btoa(`${API_USER}:${API_PASSWORD}`);
        const res = await fetch(`${BASE_URL}${API}`, {
          method: "POST",
          headers: {
            "Content-Type": "application/json",
            Authorization: `Basic ${token}`,
          },
          body: JSON.stringify({
            FUNC: "ZFM_YCTN_INFO",
            DATA: {
              IT_SAMPID: [
                {
                  SIGN: "I",
                  OPTION: "EQ",
                  LOW: maMau,
                },
              ],
            },
          }),
        });

        const data = await res.json();
        if (data?.RESPONSE?.IT_DATA?.length > 0) {
          setInfo(data.RESPONSE.IT_DATA[0]);
        } else {
          setInfo(null);
        }
      } catch (err) {
        console.error("Li gi API:", err);
      }
    };
    fetchInfo();
  }, [maMau]);

  return (
    <div
      style={{
        width: "100%",
        maxWidth: `${width}px`,
        margin: "auto",
        border: "2px solid black",
        fontFamily: "Arial, sans-serif",
        fontSize: "13px",
      }}
    >
      {/* Header */}
      <div
        style={{
          display: "flex",
          alignItems: "center",
          padding: "10px",
          borderBottom: "1px solid black",
        }}
      >
        <img
          src={logoPetro}
          alt="Logo"
          style={{ width: "60px", height: "60px", marginRight: "10px" }}
        />
        <div style={{ flex: 1, textAlign: "center", lineHeight: "1.4" }}>
          <div style={{ fontWeight: "bold" }}>TP ON XNG DU VIT NAM</div>
          <div style={{ fontWeight: "bold" }}>CNG TY XNG DU B12</div>
        </div>
      </div>

      {/* Barcode */}
      <div style={{ textAlign: "center", padding: "10px 0" }}>
        <BarcodeImg data={maMau} />
      </div>

      {/* Info */}
      <div style={{ borderTop: "1px solid black", padding: "10px" }}>
        {!info ? (
          <p>ang ti d liu...</p>
        ) : (
          <table
            style={{
              width: "100%",
              borderCollapse: "collapse",
              lineHeight: "1.5",
            }}
          >
            <tbody>
              {/* --- QR + M MU + S NIM + LOI HNG --- */}
              <tr>
                <td colSpan={2} style={{ position: "relative", paddingRight: "100px" }}>
                  {/* QR gc phi */}
                  <div style={{ position: "absolute", top: "0", right: "0" }}>
                    <QRCodeImg
                      data={`${BASE_URL}/app/yctn?maMau=${maMau}`}
                      width={90}
                    />
                  </div>

                  {/* Phn ni dung 3 dng u */}
                  <div style={{ display: "table", width: "100%" }}>
                    <div style={{ display: "table-row" }}>
                      <div style={{ display: "table-cell", width: "160px" }}>
                        <b>M MU</b>
                        <div style={{ fontSize: "11px" }}>Sample ID</div>
                      </div>
                      <div style={{ display: "table-cell" }}>
                        {maMau}
                      </div>
                    </div>

                    <div style={{ display: "table-row" }}>
                      <div style={{ display: "table-cell" }}>
                        <b>S NIM</b>
                        <div style={{ fontSize: "11px" }}>Seal No</div>
                      </div>
                      <div style={{ display: "table-cell" }}>
                        {info?.SEAL_NO || "SEL01"}
                      </div>
                    </div>

                    <div style={{ display: "table-row" }}>
                      <div style={{ display: "table-cell" }}>
                        <b>LOI HNG</b>
                        <div style={{ fontSize: "11px" }}>Goods Type</div>
                      </div>
                      <div style={{ display: "table-cell" }}>
                        {info?.CARGO || "000000000000601002"}
                      </div>
                    </div>
                  </div>
                </td>
              </tr>

              {/* --- Cc dng thng tin khc --- */}
              {[
                ["V TR LY MU", "Sampling Collected Location", info?.VTLM_T || "Mu y"],
                ["NI XUT", "Place of Release", info?.NOI_XUAT || "-"],
                ["NI LY", "Sampling Location", info?.SAMPLING_LOCATION || "B cha"],
                ["NGY/GI LY", "Sampling Date Time", info?.ISSUE_DATE || "26/09/2025 10:34 AM"],
                ["NGY HT HN LU", "Sample Retention Expiry Date", info?.SAMPLE_EXP || "26/10/2025"],
                ["NGI LY", "Sampler", info?.TAKEN_BY || "Nguyn Vn An"],
                ["NGI NHN", "Receiver", info?.RECEIVER || "Nguyn Th Lan Hng"],
              ].map(([label, sub, value], idx) => (
                <tr key={idx}>
                  <td style={{ width: "160px", verticalAlign: "top" }}>
                    <b>{label}</b>
                    <div style={{ fontSize: "11px" }}>{sub}</div>
                  </td>
                  <td style={{ verticalAlign: "top" }}>{value}</td>
                </tr>
              ))}
            </tbody>
          </table>
        )}
      </div>
    </div>
  );
};

export default YCTN;
